/* Navbar */
.navbar {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
}

.nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end;
}

.nav-item {
  margin-right: 10px;
}

.nav-link {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

/* Home Container */
.home-container {
  background-image: url("./aleks-dahlberg-3cWA3U8xb5w-unsplash.jpg");
  background-size: cover;
  background-position: center;
  padding: 50px;
  text-align: center;
  color: white;
}
